.fresh-out-the-oven {
	display: flex;
	height: 90vh;
	max-width: 1500px;
	margin: 0 auto;
	margin-top: 5vh;
	perspective: 1000px;
	transform-style: preserve-3d;
}
.fresh-out-the-oven > * {
	flex: 1 4 auto;
	padding: 2rem;
	border: 1rem double #1a1a1a;
	position: relative;
	background: #fff;
	transition: all 0.3s;
	box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
	overflow: scroll;
}

.fresh-out-the-oven > *:first-child {
	flex-shrink: 1;
	flex-basis: 50%;
}
.fresh-out-the-oven > *:nth-child(2) {
	border-left: 0;
	border-right: 0;
	min-width: 300px;
}
.fresh-out-the-oven > *:last-child {
	flex-shrink: 1;
	flex-basis: 50%;
}

